import React, { useState } from 'react';
import styles from './Space.module.css';
import SpaceApplication from '../SpaceApplication/SpaceApplication';

const Space = () => {
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const openModal = () => {
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  return (
    <section className={styles.space}>
      <div className={styles.container}>
        <h2>Spaiu gratuit pentru orice iniiativ de tineret</h2>
        <p>
          Centrul de Tineret STUDEM ofer tinerilor ansa de a organiza activiti interactive n spaiul centrului GRATUIT. 
          Pentru a profita de spaiu, citete cu atenie termenii i condiiile apoi completeaz formularul pentru aplicare. 
          Poi beneficia oricnd de spaiu i nu uita, suntem aici i acum pentru tine!
        </p>
        <div className={styles.buttons}>
          <button className={styles.button} onClick={openModal}>Aplic pentru Evenimente</button>
          <button className={styles.button}>Calendarul STUDEM</button>
        </div>
      </div>

      {/* Modalul se afieaz doar dac modalIsOpen este true */}
      {modalIsOpen && <SpaceApplication closeModal={closeModal} />}
    </section>
  );
};

export default Space;
